html, body{
  font-family: 'Times New Roman', Times, serif;
  margin: 0;
}

.flexBox{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#userRow{
  display: flex;
  justify-content: center;
  flex-direction: row;
  width: fit-content;
  flex-wrap: wrap;
}

#postCol{
  display: flex;
  flex-direction: column;
  flex: 1;
  max-width: 80%;
  padding: 10px;
}

#postCol > div{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border: 2px solid orange;
  background-color: rgb(36, 122, 202);
  color: white;
}

#postCol > div > h1{
  font-style: italic;
}

.entry{
  all: unset;
  cursor: pointer;
  padding: 5px;
  margin: 0 5px;
  background-color: orange;
  border: 2px solid rgb(36, 122, 202);
  transition: .2s;
  box-shadow: 2px 2px 2px grey;

}

.entry:hover{
  background-color: rgb(36, 122, 202);
  color: white;
  border: 2px solid orange;
}

.headerBar{
  background-color: rgb(36, 122, 202);
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}


